a {
	color: unset;
	text-decoration: none;
}

.App {
  text-align: center;
}

.EditButtons {
	display: flex;
	flex-direction: row;
	justify-content: space-around;
	margin: 2vmin;
}

.HeaderBar {
	align-items: center;
	display: flex;
	flex-direction: row;
	justify-content: space-around;
}

.HeaderTitle {
	align-self: center;
	display: inline-block;
	font-size: 1.2rem;
	font-weight: bold;
	padding: 1vh 1vw;
}

.HeaderButton {
	align-self: center;
	cursor: pointer;
	display: inline-block;
	font-size: 0.7rem;
	font-weight: bold;
	padding: 1vh 1vw;
}

.ListEditField {
	width: 95%;
}

.ListItem {
	align-content: center;
	border-bottom: 1px lightgray solid;
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	margin: 0 5vw 0 5vw;
	min-height: 2rem;
	text-align: left;
	width: 90%;
}

.ListItem:active {
	background-color: lightgray;
}

.ListTitle {
	display: block;
	font-size: 1.1rem;
	font-weight: bold;
}

.ListSubtitle {
	color: gray;
	display: block;
	font-size: 0.8rem;
	min-height: 2vh;
}

.MenuBody {
	background: white;
	border: 2px solid lightgray;
	max-width: 200px;
	position: absolute;
	right: 0;
	width: 80vw;
}

.QuietButton {
	background-color: inherit;
	border: none 0;
	color: gray;
	font-size: 0.9rem;
	padding: 1vh 1vw;
	margin: auto;
}

.QuietButton:active {
	background-color: lightgray;
}

.Text {
	padding: 0 5%;
	text-align: justify;
}

.TextField-Large {
	flex: 1;
	height: 40vh;
	vertical-align: top;
	width: 95%;
}

.center {
	flex: 1 1 70%;
}

.left {
	flex: 0 0.5 15%;
	margin: auto;
	padding: auto;
	text-align: left;
}

.right {
	flex: 0 0.5 15%;
	margin: auto;
	padding: auto;
	text-align: right;
}

.subtle {
	color: darkgray;
	font-size: 0.9rem;
}

